// forms
form,
fieldset {
  margin: 0;
  padding: 0;
  border-style: none;
}

input[type='text'],
input[type='tel'],
input[type='email'],
input[type='search'],
input[type='password'],
input[type='url'],
// input[type='date'],
textarea {
  @include animate(border-color color);
  -webkit-appearance: none;
  -webkit-border-radius: 0;
  padding: 10px 16px;
  font-size: $input-font-size;
  line-height: $input-line-height;
  font-family: $input-font-family;
  box-sizing: border-box;
  border: $input-border;
  border-radius: $input-border-radius;
  color: $gunmetal;

  &:not(textarea) {
    height: $input-height;
  }

  &:focus {
    outline: none;
    border-color: $input-focus-border-color;

    @include placeholder {
      color: $gunmetal;
    }
  }

  .input-error & {
    border-color: $orangey-red;
  }

  @include placeholder {
    @include animate(color);
    color: $placeholder-color;
  }

  &.hidden {
    display: none;
  }
}

input[type='search']::-webkit-search-decoration,
input[type='search']::-webkit-search-cancel-button,
input[type='search']::-webkit-search-results-button,
input[type='search']::-webkit-search-results-decoration {
  display: none;
  // -webkit-appearance: textfield;
}

textarea {
  resize: vertical;
  vertical-align: top;
  overflow: auto;

  @include placeholder {
    @include animate(color);
    color: $placeholder-color;
  }
}

button,
input[type='button'],
input[type='reset'],
input[type='file'],
input[type='submit'] {
  @include animate(background-color color);
  -webkit-appearance: none;
  -webkit-border-radius: 0;
  height: $submit-height;
  padding: 12px 20px;
  font-size: 18px;
  line-height: $submit-line-height;
  color: $submit-color;
  outline: none;
  cursor: pointer;
  border: 1px solid $pinkish-grey;
  border-radius: 0;
  background: $submit-background;
  font-family: $base-font-sans-serif;
  font-weight: 600;
  font-family: $base-font-sans-serif;

  &.kumya {
    border: none;

    &:hover {
      background: $submit-background;
      color: $submit-color;
    }
  }

  &:hover {
    background: $pinkish-grey;
    color: $white;
  }
}

select {
  @include animate(border-color);
  -webkit-appearance: none;
  -webkit-border-radius: 0;
  -webkit-appearance: none;
  -moz-appearance: none; 
  appearance: none;
  height: $select-height;
  width: 100%;
  padding: $select-padding;
  font-size: $select-font-size;
  line-height: $select-line-height;
  box-sizing: border-box;
  border: $select-border;
  border-radius: $select-border-radius;

  &:focus {
    outline: none;
  }

  #sidebar & {
    width: 100%;
  }

  &:focus {
    outline: none;
    border-color: $input-focus-border-color;
  }
}

/*Custom styles for checkboxes and radio buttons*/

[type="checkbox"],
[type="radio"]{
  position:fixed;
  left:0;
  top:0;
  opacity:0;
  z-index: -1;

   .input-error & {
    
    & ~ .fake-field,
    & ~ .fake-field ~ .fake-title {
      border-color: $orangey-red;
      color: $orangey-red;

      a {
        color: $orangey-red;
        transition: none;
      }
    }
  }
}

.fake-field {
  float: left;
  width: 20px;
  height: 20px;
  border: solid 3px $almost-black;
  background: transparent;
  position: relative;
  margin: 5px 14px 0 0;
  border-radius: 50%;
  cursor: pointer;

  @include media('≥tablet') {
    margin: 5px 20px 0 0;
    width: 26px;
   height: 26px;
  }

  @include media('≥desktop') {
   margin: 7px 11px 0 0;
  }
}

[type="radio"] ~ .fake-field {
  width: 26px;
  height: 26px;
}
 
[type="checkbox"] ~ .fake-field:before,
[type="radio"] ~ .fake-field:before {
  content:'';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
  opacity: 0;
  width: 10px;
  height: 10px;
  background-color: $gunmetal;
  border-radius: 50%;

  @include media('≥tablet') {
    width: 14px;
    height: 14px;
  }
}

[type="radio"] ~ .fake-field:before {
  width: 14px;
  height: 14px;
}

.fake-title,
label {
  overflow: hidden;
  display: block;
  text-align: left;
  cursor: pointer;

  a {
    color: $gunmetal;
    font-weight: 600;

    &:hover {
      color: $light-burgundy;
    }
  }
}

label {
  width: 100%;

  &.request-title {
    display: inline-block;
    vertical-align: top;
    width: 100%;
    margin-bottom: 10px;
  }

  &.note {
    width: auto;
    display: inline-block;
    vertical-align: top;
    cursor: default;
  }
}

[type="radio"] ~ .fake-field {
  margin:0 14px 0 0;

  @include media('≥tablet') {
   margin: 0 12px 0 0;
  }

   & ~ .fake-title {
    font-size: 16px;
    line-height: 1.2;
    padding: 3px 0 0;
  }
}

[type="radio"]:checked ~ .fake-field:before,
[type="checkbox"]:checked ~ .fake-field:before{
  opacity:1;
}

[type="radio"]:checked ~ .fake-field,
[type="checkbox"]:checked ~ .fake-field {
  border-color: $gunmetal;
}
form {
  .item {
    overflow: hidden;
    text-align: center;

    @include media('≥tablet') {
      text-align: right;
    }
  }
}

.agree-form {

  .row {
    margin: 0 0 30px;

    @include media('≥desktop') {
      margin: 0 0 50px;
    }

    &:last-of-type {
      margin-bottom: 0;
    }
  }
}

.request-form,
.agree-form,
.wpcf7-form {
  button[type="submit"] {
    min-width: 255px;

    @include media('<tablet') {
      width: 100%;
      max-width: 290px;
    }
  }
}

.input-box {
  padding: $input-padding;
  cursor: text;
  color: $dusty-lavender;
  font-size: 0;
  line-height: 0;
  padding: 0;

  .input-error & {
    color: $orangey-red;

    @include media('≥tablet') {
      max-width: 50%;
      font-size: 16px;
      line-height: 18px;
      padding-right: 0;
      white-space: nowrap;
      padding-left: 20px;
    }
  }

  &:after {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0;
    opacity: 0;
    content: '';
    height: 4px;
    background: $gunmetal;
    transition: width 0.4s linear, opacity 0.4s linear;
  }

  .form-control + & {
    padding-right: 20px;
  }
}

.input-label {
  background: $white;
  border: 3px solid $white;

  @include media('≥tablet') {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: flex-end;
  }

  .input-error &  {
    border-color: $orangey-red;
  }
}

.request-form,
.wpcf7-form {
  position: relative;

  input[type='text'],
  input[type='tel'],
  input[type='email'],
  textarea,
  .form-control { 
    border: none;

    &:focus {
      & + .input-box {
        font-size: 0;
        line-height: 0;

        &:after {
          width: 100%;
          opacity: 1;
        }
      }
    }
  }

  input[type='text'],
  input[type='tel'],
  input[type='email'] {
    height: 44px;
    padding-right: 14px;
    padding-left: 14px;
  }

  .wpcf7-form-control-wrap,
  .row {
    @extend %clearfix;
    margin-bottom: 18px;
    position: relative;
    display: block;

    @include media('≥tablet') {
      margin-bottom: 14px;
    }

    &:before {
      content: "!";
      position: absolute;
      bottom: 7px;
      right: 0;
      width: 38px;
      height: 35px;
      text-align: center;
      border-left: 1px solid $green;
      display: block;
      color: $orangey-red;
      font-size: 20px;
      line-height: 34px;
      font-weight: 700;
      display: none;
    }

    &.input-error {

       &:before {
        display: block;
      }

      .input-label {
        border-color: $orangey-red;
      }

      input[type='text'],
      input[type='tel'],
      input[type='email'] {
        padding-left: 4px;
        padding-right: 38px;
      }
    }

    &.request-submit {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      padding-top: 14px;
      margin-bottom: 0;

      @include media('≥tablet') {
        flex-wrap: nowrap;
      }

      .note {
        order: 2;
        width: 100%;

        @include media('≥tablet') {
          order: 1;
          width: calc(100% - 255px);
        }
      }

      .item {
        width: 100%;
         order: 1;
         margin-bottom: 28px;

        @include media('≥tablet') {
          order: 2;
          width: auto;
          margin-bottom: 0;
        }
      }
    }

    &.form-row {
     display: flex;
     position: relative;
     z-index: 1;
     align-items: center;
     font-size: 16px;
     line-height: 26px;
     margin: 0 0 26px;

      input[type='text'] {
        height: 50px;
        padding-right: 16px;
        padding-left: 16px;
        color: $placeholder-color;
        cursor: pointer;
      }

      .input-label {
        border: none;
      }
    }

    &.label-row {
      margin: 20px 0 0;

      @include media('≥tablet') {
        margin: 30px 0 0;
      }
    }

    &.textarea-row,
    &.message {

      .input-label {
        height: auto;
      }
    }
  }

  textarea {
    min-height: 165px;
    padding-right: 16px;
  }

  label {
    font-size: 18px;
    line-height: 1.44;

    &.input-label {
      background: $white;
      border: 3px solid $white;
      height: 50px;
    }
  }

  .fake-title,
  .wpcf7-list-item-label {
    float: right;
    padding: 3px 0 0 38px;
    font-size: 16px;
    line-height: 1.2;
  }

  .fake-field {
    margin: 0 11px 0 0;
    display: block;
    float: none;
  }
}

.time-group {
  
  @include media('≥desktop') {
    width: 440px;
  }
}

.input-group {
  position: relative;
  cursor: pointer;

  .wpcf7-form-control-wrap {
    margin-bottom: 0;
  }
}

.data-group {
  width: 142px;
  margin: 0 14px 0 0;

  @include media('≥tablet') {
    width: 262px;
    margin: 0 27px 0 0;
  }

  @include media('≥desktop') {
    margin: 0 20px 0 0;
  }
}

.wpcf7-radio {
  @extend %listreset;
  display: flex;
  flex-wrap: wrap;
  width: auto;
  margin: 10px -10px 0;

  li,
  .wpcf7-list-item {
    max-width: 50%;
    padding: 0 10px;
    margin: 0;
  }
}

span.wpcf7-not-valid-tip {
  display: none !important;
}

div.wpcf7-response-output {
 margin-left: 0;
 margin-right: 0;
}

.hidden-datatimepicker {
  display: none;
}

div.wpcf7-mail-sent-ok {
  color: $green;
  border: none;
  padding: 0;
}