// Typography

@include title-size('≥');

h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6,
.h {
  font-family: $headings-font-family;
  color: $headings-color;
  font-weight: 400;
  margin: 0 0 10px;
  line-height: 1.3;
  
  @include media('≥desktop') {
    margin-bottom: 16px;
  }

  &.burgundy-heading {
    color: $light-burgundy;

    a {
      color: $light-burgundy;

      &:hover {
        color: $pinkish-grey;
      }
    }
  }

  a {
    font-weight: 400;
  }
}

h1 {
  line-height: 1.2;

  @include media('≥tablet') {
    line-height: 1.15;
  }
}

h2 {
  line-height: 1.4;
  margin: 0 0 30px;

  @include media('≥tablet') {
    line-height: 1.18;
    margin: 0 0 25px;
  }

  @include media('≥desktop') {
    line-height: 1.3;
    margin-bottom: 22px;
  }

  @include media('≥widescreen') {
    margin-bottom: 20px;
  }
}

p {
  margin: 0 0 30px;
}

a {
  @include animate(color);
  color: $base-link-color;
  text-decoration: $text-decoration;
  font-weight: 600;

  &:hover {
    color: $base-link-hover-color;
    text-decoration: $text-decoration-hover;
  }
}

a[href*="mailto:"] {
  word-wrap: break-word;
}
