@-webkit-keyframes buttonHover {
  from {
    opacity: 0;
    width: 0
  }
  to {
    opacity: 1;
    width: 100%
  }
}

@keyframes buttonHover {
  from {
    opacity: 0;
    width: 0
  }
  to {
    opacity: 1;
    width: 100%
  }
}

#header {
  position: relative;
  z-index: 5;
}

#burger-btn { 
  position: fixed;
  top: 100px;
  left: 0;
  width: auto;
  transition: all .6s;
  z-index: 2;

  @include media('≥tablet') {
    top: 120px;
  }

  @include media('≥widescreen') {
    top: 150px;
  }

  .nav-active & {
    z-index: 4;
    top: 100px;
    
    @include media('≥tablet') {
      top: 120px;
    }

    @include media('≥widescreen') {
      top: 150px;
    }

    &.fixed-position {
      top: 100px !important;
      
      @include media('≥tablet') {
        top: 120px !important;
      }

      @include media('≥widescreen') {
        top: 150px !important;
      }
    }

    .nav-opener {

      > span {
        margin: 0;
      }
    }
  }

  .logo {
    display: block;
    width: 65px;
    text-align: center;
    height: 65px;
    margin: 65px 0 0;
    transform: translateX(0);
    position: relative;
    background-color: $white;
    transition: all .6s cubic-bezier(.165, .84, .44, 1);
    display: flex;
    justify-content: center;
    align-items: center;

    @include media('≥tablet') {
      width: 90px;
      margin: 0 0 0 90px;
      height: 90px;
    }

    img {
      width: 40px;
      display: block;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      max-width: 100%;

      @include media('≥tablet') {
        width: 56px;
      }
    }
  }

  .nav-opener {
    position: absolute;
    top: 0;
    left: 0;
    width: 65px;
    height: 65px;
    display: block;
    transition: border-color .3s;
    background-color: $gunmetal;
    border-style: solid;
    border-color: $gunmetal;
    border-width: 1px;

    @include media('≥tablet') {
      width: 90px;
      height:  90px;
    }

    > span {
      width: 22px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      display: block;
      margin: 0 0px 0 -1px;

      @include media('≥tablet') {
        width: 30px;
      }

      i {
        width: 22px;
        height: 3px;
        background: $white;
        display: block;
        margin: 0 0 2px;
        overflow: hidden;
        transition: all .3s;
        opacity: 1;

        @include media('≥tablet') {
          width: 30px;
          height: 4px;
          margin: 0 0 3px;
        }

        &:nth-child(1),
        &:nth-child(2),
        &:nth-child(3) {

          .nav-active & {
            animation: none;
          }
        }
        
        &:nth-child(1) {

          .nav-active & {
            transform: rotate(45deg) translate(2px, 5px) scale(1.4);
            opacity: 1;
            width: 22px;

            @include media('≥tablet') {
              width: 30px;
              transform: rotate(45deg) translate(5px, 5px);
            }
          }
        }

        &:nth-child(2) {
          .nav-active & {
            opacity: 0;
          }
        }

        &:nth-child(3) {
          .nav-active & {
            transform: rotate(-45deg) translate(2px, -5px) scale(1.4);
            opacity: 1;
            width: 22px;

            @include media('≥tablet') {
              width: 30px;
              transform: rotate(-45deg) translate(5px, -5px);
            }
          }
        }

        &:last-child {
           margin-bottom: 0;
        }
      }
    }

    &:hover {

      span i {
        &:nth-child(1),
        &:nth-child(2),
        &:nth-child(3) {

          .nav-active & {
            animation: none;
          }
        }

        &:nth-child(1) {
          animation: buttonHover .3s linear .3s forwards;
        }

        &:nth-child(2) {
          animation: buttonHover .3s linear .5s forwards;
        }

        &:nth-child(3) {
          animation: buttonHover .3s linear .7s forwards;
        }
      }
    }

    .nav-active & {
      border-color: $white;
    }
  }
}

.resize-active #burger-btn {
  -webkit-transition: none!important;
  transition: none!important
}

/* Nav styles*/

#nav {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  opacity: 0;
  visibility: hidden;
  -webkit-transition: all .6s cubic-bezier(.645, .045, .355, 1);
  transition: all .6s cubic-bezier(.645, .045, .355, 1);
  background-color: $gunmetal;
  overflow-x: hidden;
  overflow-y: auto;
  z-index: 3;
  padding-top: 70px;
  width: 100%;

  @include media('≥tablet') {
    display: flex;
    align-items: center;
    align-content: center;
    flex-direction: column;
    padding-top: 89px;
  }

  .container {
    max-width: 860px;
    padding: $padding-container;
    margin: 0 auto;
  }

  .nav-active & {
    opacity: 1;
    visibility: visible;
    z-index: 3;
  }
}

.main-menu {
  @extend %clearfix;
  color: $white;
  counter-reset: count;
  width: 100%;
  padding: 196px 0 50px 50px;

  @include media('≥tablet') {
    flex-direction: column;
    flex: 1 0 auto;
    padding: 152px 45px 50px;
    max-width: 472px;
  }

  @include media('≥desktop') {
    padding: 152px 45px 50px;
    max-width: 100%;
  }

  @include media('≥widescreen') {
    padding: 133px 45px 50px;
  }

  .container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .column {
    width: 100%;

    @include media('≥desktop') {
      width: auto;
      padding: 0 0 0 50px;
      max-width: 50%;
    }
  }

  ul {
    @extend %listreset;
    font-size: 20px;
    line-height: 1;
    font-family: $base-font-serif;
    margin-bottom: 7px;

    @include media('≥tablet') {
      font-size: 36px;
      margin-bottom: 32px;
    }

    @include media('≥desktop') {
      margin-bottom: 7px;
    }

    &.submenu {
      font-size: 14px;
      line-height: 1.2;
      margin-bottom: 7px;

      @include media('≥tablet') {
        font-size: 18px;
      }

      @include media('≥desktop') {
        padding: 10px 0 0;
      }

      li {
        padding: 20px 0 0;

        @include media('≥tablet') {
          padding: 13px 0 3px;
        }

        @include media('≥desktop') {
          padding: 5px 0 0;
        }
        
        &:after {
          display: none;
        }
      }

      a {
        font-weight: 400;
      }
    }

    > li {
      padding: 0 0 28px;

      &:after {
        display: none;
      }


      &.active {
        > a {
          color: $pinkish-grey;

          span {
            
            &:after {
              width: 30px;
            }

            &:before {
              transform: translateX(-30px);
              visibility: hidden;
              opacity: 0;
            }
          }
        }
      }

      > a {
        text-decoration: none;
        position: relative;
        color: $white;
        display: block;
        font-weight: 400;

        @include media('≥tablet') {
          display: inline;
        }

        > span {
          display: inline-block;
          vertical-align: text-bottom;
          bottom: 4px;
          left: -28px;
          position: absolute;
          width: 30px;
          font-family: $base-font-sans-serif;
          pointer-events: none;

          @include media('≥tablet') {
            bottom: 12px;
            left: -30px;
           }

          &:after {
            content: "";
            width: 0;
            height: 1px;
            background: $pinkish-grey;
            display: block;
            transition: width .3s cubic-bezier(.645, .045, .355, 1);
            bottom: 1px;
            right: 9px;
            position: absolute;
          }

          &:before {
            bottom: 0;
            left: 0;
            position: absolute;
            counter-increment: count;
            content:''counter(count, decimal-leading-zero)'. ';
            font-size: 12px;
            line-height: 1;
            transform: translateX(0);
            transition:  visibility .3s cubic-bezier(.645, .045, .355, 1), transform .3s cubic-bezier(.645, .045, .355, 1), opacity .3s cubic-bezier(.645, .045, .355, 1);
          }
        }

        &:hover {
          color: $pinkish-grey;

          span {
            
            &:after {
              width: 30px;
            }

            &:before {
              transform: translateX(-30px);
              visibility: hidden;
              opacity: 0;
            }
          }
        }
      }
    }
  }
}

/* Menu container styles*/

.menu-container {
  width: 100%;
  text-align: center;
  color: $white;
  padding: 0 0 7px 50px;
  font-size: 16px;
  line-height: 1.2;
  font-family: $base-font-sans-serif;
  flex-shrink: 0;
  min-width: 100%;

  @include media('≥tablet') {
    padding: 0 0 23px;
  }

  @include media('≥desktop') {
    padding: 0 0 34px;
    min-width: 920px;
  }

  .container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    max-width: 860px;

    @include media('≥tablet') {
      justify-content: flex-start;
    }

    @include media('≥desktop') {
      justify-content: space-between;
    }

    .column {
      width: 100%;
      padding: 0 0 24px;
      display: flex;
      flex-wrap: wrap;
      justify-content: flex-start;
      
      @include media('≥tablet') {
        padding: 0 10px;
        max-width: 25%;
        justify-content: center;
      }

      @include media('≥desktop') {
        max-width: 30%;
      }

      &:last-of-type {
        @include media('≥tablet') {
          max-width: 40%;
        }
      }
    }
  }
}

.menu-title {
  font-size: 18px;
  line-height: 1.2;
  display: inline-block;
  vertical-align: top;
  position: relative;
  letter-spacing: 0.05em;
  padding: 0 0 10px;
  margin-bottom: 23px;

  @include media('≥tablet') {
    font-size: 22px;
    padding: 0 0 20px;
    margin-bottom: 36px;
  }

  &:after {
    display: block;
    bottom: 0;
    left: 0;
    height: 2px;
    width: 60px;
    background-color: $white;
    content: '';
    position: absolute;

    @include media('≥tablet') {
      height: 3px;
    }
  }
}

/*languages-menu  styles*/

.languages-menu ,
.languages-menu ul{
  @extend %listreset;
  color: $white;
  display: flex;
  font-size: 18px;
  line-height: 1.2;
  letter-spacing: 0.9px;
  font-weight: 400;

  li {
    position: relative;
    padding: 1px 0 0 11px;
    margin: 0 0 0 13px;

    &:before {
      display: block;
      content: "|";
      position: absolute;
      left: -4px;
      top: 3px;
      background-color: transparent;
      width: auto;
      height: auto;
      border-radius: 0;
    }

    &:after {
      display: none;
    }

    &:first-of-type {
      padding-left: 0 !important;
      margin: 0 !important;

      &:before {
        display: none;
      }
    }

    &.current,
    &.wpml-ls-current-language{
      a {
        color: $white;
        font-weight: 600;
      }
    }

    a {
      color: $white;
      padding: 0;
      line-height: 1.2 !important;

      &:hover {
        color: $pinkish-grey;
      }
    }
  }
}

.wpml-ls-legacy-list-horizontal {
  padding: 0 !important;
  border: none !important;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.wpml-ls-legacy-list-horizontal a {
  display: inline !important;
}

.wpml-ls-legacy-list-horizontal .wpml-ls-item {
  padding: 1px 0 0 11px !important;
  margin: 0 0 0 13px !important;
}

.social-links {
  @extend %listreset;
  display: flex;
  flex-wrap: wrap;
  align-items: center;

  li {
    padding: 0 28px 5px 0;

    @include media('≥tablet') {
      padding: 2px 18px 2px 0;
    }
  }

  a {
    color: $white;

    &:hover {
      color: $pinkish-grey;
    }
  }

  .icon {
    display: block;
    overflow: hidden;
    text-indent: -9999px;
    font-size: 0;
    line-height: 0;

    &.icon-twitter {
      &:before {
        font: 18px/1 'icomoon';
      }
    }

    &:before {
      text-indent: 0;
      display: block;
      font: 16px/1 'icomoon';
    }
  }
}

.resize-active .nav-drop,
.resize-active .nav-drop > ul {
  transition:none !important;
}

/* Top bar styles */

.page-title {
  font-size: 16px;
  line-height:  20px;
  letter-spacing: 0.7px;
  font-weight: 600;
  width: 80%;

  @include media('≥tablet') {
    width: 70%;
    font-size: 14px;
    line-height: 2.14;
  }

  @include media('≥desktop') {
    font-size: 16px;
    line-height: 1.88;
    letter-spacing: 0.8px;
    width: 50%;
  }

  @include media('≥widescreen') {
    font-size: 18px;
    line-height: 1.67;
    letter-spacing: 0.06em;
  }
}

.top-title,
.bottom-title {
  display: inline-block;
  vertical-align: top;
}

.bottom-title {
  
  @include media('<tablet') {
    font-size: 12px;
   width: 100%;
  }

  &:before {
    content: '-';
    margin: 0 4px;
    font-weight: 600;
    display: none;

    @include media('≥tablet') {
      display: inline-block;
      vertical-align: top;
    }
  }
}

.contact-menu {
  @extend %listreset;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  font-size: 14px;
  line-height: 1.2;
  width: 20%;

  @include media('≥tablet') {
    width: 30%;
  }

  @include media('≥desktop') {
    width: 50%;
  }

  @include media('≥widescreen') {
    font-size: 16px;
  }

  li {
     @include media('≥tablet') {
      padding: 5px 0 5px 30px;
    }

    @include media('≥desktop') {
      padding: 5px 0 5px 25px;
    }

    @include media('≥widescreen') {
      padding: 5px 0 5px 32px;
    }

    &.letter {
      display: none;

      @include media('≥tablet') {
        display: block;
      }
    }
  }

  a {
    color: $gunmetal;
    font-weight: 400;

    &:hover {
      color: $pinkish-grey;
    }
  }

  .icon {
    display: inline-block;
    vertical-align: middle;
    font-size: 16px;
    line-height: 1.2;

    @include media('≥desktop') {
      padding: 0 10px 0 0;
    }

    @include media('≥widescreen') {
      padding: 0 14px 0 0;
    }

    &.icon-phone {
      font-size: 14px;
    }

    &.icon-letter {
      @include media('≥widescreen') {
        padding-top: 2px;
      }
    }
  }

  .data {
    display: none;

    @include media('≥desktop') {
      display: inline-block;
      vertical-align: middle;
    }
  }
}

.top-bar {
  padding: 13px 0;
  min-height: 70px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 6;
  background-color: $white;

  @include media('≥tablet') {
    padding: 29px 0;
    min-height: 89px;
  }

  .container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    max-width: 100%;
  }
}

.bottom-menu {
  @extend %listreset;
  display: flex;
  flex-wrap: wrap;
  font-weight: 600;
  font-size: 16px;
  line-height: 1.2;

  li {
    padding: 0 0 10px;
    width: 100%;
    text-align: left;

    @include media('≥tablet') {
      padding: 6px 0 0 30px;
      width: auto;
      text-align: center;
    }

    &.active {
      a {
        color: $pinkish-grey;
      }
    }
    &:first-of-type {
      padding-left: 0;
    }
  }

  a {
    color: $white;

    &:hover {
      color: $pinkish-grey;
    }
  }
}