#content {
  @extend %clearfix;
  width: 100%;
  padding-bottom: 15px;
  position: relative;
  z-index: 0;

  @include media('≥desktop') {
    padding-bottom: 30px;
  }

  > .section.contact-section {

    &:last-of-type {
      padding-bottom: 0;
    } 
  }

  &.blog-overview {
    overflow: visible;
  }
}

#sidebar {
  select {
   
    @include media('≥desktop') {
       max-width: 300px;
    }
  }
}
.widget {
  @extend %clearfix;
  margin-bottom: $widget-margin;

  select {
    min-width: 150px;
    width: 100%;
  }
}

ul:not([class]) {
  @extend %listreset;
  line-height: 1.4;
  margin-bottom: 30px;

  > li {
    position: relative;
    padding: 0 0 3px 36px;
    
    &:after {
      position: absolute;
      left: 11px;
      top: 10px;
      content: '';
      background-color: $gunmetal;
      width: 4px;
      height: 4px;
      border-radius: 50%;

      @include media('≥desktop') {
        top: 15px;
      }
    }
  }
}

ol:not([class]) {
  counter-reset: defaultcount;
  list-style: none;
  padding-left: 0;

  > li {
    padding-left: 35px;
    position: relative;

    &:before {
      counter-increment: defaultcount;
      content:''counter(defaultcount)'. ';
      display: inline-block;
      vertical-align: top;
      left: 10px;
      position: absolute;
      top: 0;
    }
  }
}

.search-form,
.post-password-form {
  @extend %clearfix;
  margin-bottom: 10px;

  label {
    display: block;
  }

  p {
    @extend %clearfix;
  }

  input {
    float: left;
    margin-right: 5px;
    margin-bottom: 5px;
    width: 100%;
    
    @include media('≥tablet') {
      max-width: 300px;
      width: auto;
    }

    #sidebar & {
      @include media('≥tablet') {
        margin: 0 0 5px 5px;
      }

      &[type='submit'] {
        margin: 0;
        width: auto;

        @include media('≥tablet') {
          margin: 0 0 5px 5px;
        }
      }
    }

    &[type='submit'] {
      min-width: 255px;
      margin: 0;

      @include media('≥tablet') {
        min-width: 0;
      }
    }
  }
}

.nav-links,
.navigation,
.navigation-comments {
  position: relative;
  vertical-align: middle;
  margin: 0 0 $vertical-rhythm;

  .screen-reader-text {
    position: absolute;
    left: -99999px;
  }

  .page-numbers {
    margin: 0 3px;
    display: inline-block;
    vertical-align: middle;

    &:first-child {
      margin-left: 0;
    }

    &:last-child {
      margin-right: 0;
    }
  }

  .next,
  .prev {
    @include animate(background-color color);
    display: inline-block;
    vertical-align: middle;
    background: $btn-background;
    color: $btn-color;
    border: 1px solid $pinkish-grey;
    padding: $btn-padding;
    font-size: $btn-font-size;
    line-height: $btn-line-height;
    max-width: 49%;
    font-weight: 600;
    margin-top: 0;

    &:hover {
      background: $pinkish-grey;
      color: $btn-hover-color;
    }

    @include media('<tablet') {
      font-size: 0;

      &:after {
        content: '»';
        font-size: $btn-font-size;
      }
    }
  }

  .prev {
    @include media('<tablet') {
      &:after {
        content: '«';
      }
    }
  }
}

.navigation-single {
  margin-bottom: $vertical-rhythm;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  justify-content: space-between;
  align-items: stretch;

  .next,
  .prev {
    max-width: 49%;
    display: flex;
    align-items: center;
    padding: 0;
    margin: 0;
    border: 0;

    a {
      @include animate(background-color color);
      display: block;
      background: $btn-background;
      color: $btn-color;
      border: 1px solid $pinkish-grey;
      padding: $btn-padding;
      font-size: $btn-font-size;
      line-height: $btn-line-height;
      font-weight: 600;

      @include media('<tablet') {
        font-size: 0;

        &:after {
          content: '»';
          font-size: $btn-font-size;
        }
      }

      &:hover {
        background: $pinkish-grey;
        color: $btn-hover-color;
      }
    }
  }

  .prev {
    a {
      @include media('<tablet') {
        &:after {
          content: '«';
        }
      }
    }
  }
}

.comment-form {
  label {
    display: block;
    padding-bottom: 4px;
  }

  input,
  textarea {
    margin: 0 4px 0 0;
    width: 100%;

    @include media('≥tablet') {
      max-width: 300px;
    }

    &[type='submit'] {
      display: block;
      width: auto;
    }
  }

  textarea {
    overflow: auto;
    margin: 0;
    height: 120px;
    min-height: 120px;
  }
}

.commentlist {
  margin: 0 0 1.2em;

  .edit-link {
    margin: 0;
  }

  .avatar-holder {
    float: left;
    margin: 0 1.2em 4px 0;
  }
}

.commentlist-item {
  .commentlist-item {
    padding: 0;
    @include media('≥tablet') {
      padding: 0 0 0 2em;
    }
  }
}

.comment,
.commentlist-holder {
  overflow: hidden;
}

.commentlist-item .commentlist-item,
.commentlist-item + .commentlist-item {
  padding-top: 20px;
}

.widget_calendar {
  th,
  td {
    text-align: center;
  }

  #prev {
    text-align: left;
  }

  #next {
    text-align: right;
  }

  #sidebar & {

    @include media('≥desktop') {
      max-width: 300px;
    }
  }
}

#wp-calendar {
  width: 100%;
}

.wp-caption {
  max-width: 100%;
  clear: both;
  border: $btn-border;
  padding: 4px;
  margin-bottom: 20px;
  text-align: center;

  img {
    margin-bottom: 4px;
    max-width: 100%;
  }

  p {
    margin: 0;
  }
}

.aligncenter {
  margin: 0 auto $vertical-rhythm;
}

img {
  &.aligncenter {
    display: block;
    margin: 0 auto;
  }
}

.alignleft {
  float: left;
  margin: 0 1.2em 4px 0;
}

.alignright {
  float: right;
  margin: 0 0 4px 1.2em;
}

.mejs-container {
  margin-bottom: $vertical-rhythm;
}

.wp-caption-text,
.gallery,
.alignnone,
.gallery-caption,
.sticky,
.bypostauthor {
  height: auto;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  width: 100%;

  .gallery-item {
    margin: 0;
    padding: 0 10px 10px;

    dt {
      float: none;
      width: 100%;
      margin: 0 0 10px;
      padding: 0;
    }
  }
}

.gallery-columns-2 {
  .gallery-item {
    max-width: 50%;
    width: 50%;
  }
}

.gallery-columns-3 {
  .gallery-item {
    max-width: 33.33%;
    width: 33.33%;
  }
}

.tagcloud {
  @extend %listreset;
  display: flex;
  flex-wrap: wrap;
}

.comment-list {
  .comment-body {
    margin-bottom: 20px;
  }
}

.comment-author {
  img {
    margin-right: 10px;
  }
}

