.blog-description {
  display: flex;
  flex-wrap: wrap;
  font-size: 12px;
  line-height: 1.2;
  margin-bottom: 10px;

  @include media('≥tablet') {
    margin-bottom: 5px;
  }

  @include media('≥widescreen') {
    margin-bottom: 14px;
  }

  .article-description & {
    margin-bottom: 8px;

    @include media('≥widescreen') {
      margin-bottom: 6px;
    }
  }
}

.details-list {
  @extend %listreset;
  @include animate(color);
  text-transform: uppercase;
  display: flex;
  flex-wrap: wrap;
  margin: 0 10px 0 0;

  li {
    padding: 0 4px 5px 0;

    @include media('≥widescreen') {
      padding: 0 6px 5px 0;
    }

    & +  li {
      &:before {
        @include animate(background-color);
        display: inline-block;
        vertical-align: middle;
        content: '';
        margin: 0 4px 2px 0;
        width: 3px;
        height: 1px;
        background-color: $white;

        @include media('≥widescreen') {
          margin: 0 6px 2px 0;
        }
      }
    }
  }
}

.details-link {
  @include animate(border-color);
  position: absolute;
  bottom: 30px;
  right: 30px;
  width: 36px;
  height: 36px;
  display: block;
  border-radius: 50%;
  text-indent: -999px;
  overflow: hidden;
  border: 1px solid $white;

  @include media('≥tablet') {
    width: 60px;
    height: 60px;
  }

   &:after {
    @include animate(color);
    content: "\e90a";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateY(-50%) translateX(-50%);
    font: 14px/1 'icomoon';
    margin: 0 0 0 2px;
    text-indent: 0;
    display: block;

    @include media('≥tablet') {
      font: 22px/1 'icomoon';
    }
  }
}

.effect-apollo  {
  overflow: hidden;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;

  &:before {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(255,255,255,0.5);
    content: '';
    transition: transform 1.2s;
    transform: scale3d(1.9,1.4,1) rotate3d(0,0,1,45deg) translate3d(0,-150%,0);
  }

  &:hover {
    &:before {
      transform: scale3d(1.9,1.4,1) rotate3d(0,0,1,45deg) translate3d(0,150%,0);
    }
  }
}

.filter-list {
  @extend %listreset;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  margin: 15px 0;

  @include media('≥tablet') {
    margin: 26px 0 56px;
  }

  @include media('≥desktop') {
    margin: 32px 0 45px;
  }

  li {
    padding: 0 15px 15px 0;
  }

  [type="checkbox"]{
    position:fixed;
    left:0;
    top:0;
    opacity:0;
    z-index: -1;
  }

  .fake-input{
    @include animate(background border-color);
    display: inline-block;
    width:100%;
    vertical-align: top;
    position: relative;
    cursor: pointer;
    background-color: $white;
    padding: 6px 12px;
    border: 1px solid $silver;
    font-size: 16px;
    line-height: 18px;

    &:hover { 
      border-color: $pinkish-grey;
    }

    &:after {
      width: 101%;
    }
  }

  .fake-label {
    font-weight: 400;
    cursor: pointer;
    color: $gunmetal;
    display: inline-block;
    vertical-align: top;
  }

  [type="checkbox"]:checked + .fake-input {
    background: $pinkish-grey;
    border-color: $pinkish-grey;

    .fake-label {
      color: $white;
    }
  } 

  [type="checkbox"]:checked ~ .fake-label {
    color:$white;
  }

  [type="checkbox"]:disabled + .fake-input,
  [type="checkbox"]:disabled ~ .fake-label{
    opacity: .5;
  }
}

/*Articles styles*/

.new-item {
  opacity: 0;
  transform: translateY(50px);
}

.frame {
  margin: 0 -30px;

  @include media('≥tablet') {
    margin: 0;
  }
}

.articles-columns {
  display: flex;
  flex-wrap: wrap;
  transition: all .8s cubic-bezier(.165, .84, .44, 1);
  align-items: stretch;

  @include media('≥desktop') {
    margin: 0 -15px;
  }

  &:last-of-type {
    .column {
      &:last-of-type {
      
        @include media('<desktop') {
          margin-bottom: 15px;
        }
      }
    }
  }

  .column {
    width: 100%;
    margin-bottom: 30px;

    @include media('≥desktop') {
      width: 50%;
      padding: 0 15px;
      margin-bottom: 0;
    }

    &:nth-child(2n+1) {

      @include media('≥desktop') {
        margin-bottom: 60px;
      }
    }

    &:nth-child(2n) {

      @include media('≥desktop') {
        margin-top: 60px;
        margin-bottom: 0;
      }

      .blog-details & {
        @include media('≥desktop') {
          margin-top: 0;
          margin-bottom: 60px;
        }
      }
    }
  }
}

.article-image {
  min-height: 240px;
  width: 100%;
  background-size: cover;

  @include media('≥tablet') {
    min-height: 400px;
  }

  @include media('≥desktop') {
    min-height: 352px;
  }

  @include media('≥widescreen') {
    min-height: 400px;
  }
}

.article-description {
  @include animate(background-color color);
  display: block;
  position: relative;
  background-color: $light-beige;
  padding: 30px 30px 86px;
  font-size: 16px;
  line-height: 23px;
  min-height: 300px;

  @include media('≥tablet') {
    margin: -80px 84px 0;
    padding: 30px 30px 100px;
  }

  @include media('≥desktop') {
    min-height: 353px;
    margin: -80px 15px 0;
    padding: 30px 30px 76px;
  }

  @include media('≥widescreen') {
     margin: -78px 30px 0;
     padding: 30px 30px 92px;
  }

  &:focus {
    outline: none;
  }

  &:hover {
    background-color: $pinkish-grey;
    color: $white;

    h1,h2,h3,h4,h5,h6 {
      color: $white
    }

    .details-list {
      color: $white;

      li {
        &:before {
          background-color: $white;
        }
      }
    }

    .details-date {
      color: $white;
    }

    .details-link {
      border-color: $white;

      &:after {
        color: $white;
      }
    }
  }

 .blog-details & {
    min-height: 230px;
  }

  .details-list {
    color: $dusty-lavender;

    li {
      padding: 0 4px 5px 0;

      &:before {
        background-color: $dusty-lavender;
        margin: 0 4px 0 0;
      }
    }
  }

  .details-date {
    @include animate(color);
    color: $dusty-lavender;
  }

  .details-link {
    width: 36px;
    height: 36px;
    border-color: $dusty-lavender;

    @include media('≥tablet') {
      width: 50px;
      height: 50px;
    }

    @include media('≥desktop') {
      width: 60px;
      height: 60px;
    }

     &:after {
      font: 14px/1 'icomoon';
      color: $almost-black;

      @include media('≥tablet') {
        font: 18px/1 'icomoon';
      }

      @include media('≥desktop') {
        font: 22px/1 'icomoon';
      }
    }
  }

  h1,h2,h3,h4,h5,h6 {
    @include animate(color);
    color: $light-burgundy;
  }


  h3 {
    font-size: 25px;
    margin-bottom: 8px;

    @include media('≥tablet') {
      margin-bottom: 20px;
    }

    @include media('≥desktop') {
      margin-bottom: 9px;
    }

    @include media('≥widescreen') {
      margin-bottom: 16px;
    }
  }
}

/*Blog details page*/

.tags-list {
  @extend %listreset;
  display: flex;
  flex-wrap: wrap;
  font-size: 16px;
  line-height: 1.2;
  margin-bottom: 40px;

  li {
    padding: 0 15px 15px 0;
  }

  a {
    @include animate(border-color);
    display: block;
    padding: 5px 9px 6px;
    border: solid 1px $silver;
    font-weight: 400;

    &:hover,
    &:focus {
      border-color:$pinkish-grey;
    }

     &:after {
      width: 102%;
    }
  }
}

.social-buttons {
  @extend %listreset;
  display: flex;
  flex-wrap: wrap;

  @include media('≥widescreen') {
    margin-top: 60px;
  }

  li {
    padding: 0 15px 15px 0;
    display: block;
  }

  a {
    @include animate(color border-color);
    display: block;
    text-indent: -9999px;
    overflow: hidden;
    font-size: 0;
    line-height: 0;
    position: relative;
    color: $gunmetal;
    height: 34px;
    width: 34px;
    box-sizing: border-box;
    outline: none;
    border: 1px solid $gunmetal;
    border-radius: 50%;

    &:hover {
      color: $light-burgundy;
      border-color: $light-burgundy;

      &.linkendin {

        &:before {
          border-color: $light-burgundy !important;
        }
      }
    }

    &:before {
      @include animate(border-color);
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translateY(-50%) translateX(-50%);
      content: "";
      display: block;
      text-indent: 0;
      font-size: 20px;
      line-height: 1;
      font-family: 'FontAwesome';
      font-weight: 400;
    }

    &.twitter {
      &:before {
        content:"\f099";
      }
    }

    &.linkendin {

      &:before {
        content: "\f0e1";
        font-size: 12px;
        border: 1px solid $gunmetal;
        padding: 1px 2px;
        border-radius: 2px;
      }
    }

    &.facebook {

      &:before {
        content: "\f09a";
      }
    }
  }

  .stLarge,
  .stButton {
    background-image: none !important;
    position: absolute;
    top: 0;
    left: 0;
    height: 34px !important;
    width: 34px !important;
    box-sizing: border-box;
    outline: none;
    margin: 0 !important;
  }
}

.visual {
  min-height: 480px;
  background-size: cover;
}

.article-content {

  h3 ~ h3 {
    margin-top: 60px;
    margin-bottom: 25px;
  }

  .section ~ .section {
    padding-top: 0;
    padding-bottom: 30px;
  }
}