.animations .anim-o,
.animations .anim-t {
  transition: all .8s cubic-bezier(.165, .84, .44, 1)
}

.animations .anim-o {
  opacity: 0;

  .showed {
    opacity: 1;
  }
}

.animations .anim-t {
  transform: translateY(50px);

  &.showed {
    transform: translateY(0)
  }
}

.animations .anim-ro,
.animations .anim-to,
.animations .anim-lo  {
  transition: all .8s cubic-bezier(.165, .84, .44, 1);
}

.animations .anim-to {
  transform: translateY(50px);
  opacity: 0;

  &.showed {
    opacity: 1;
    transform: translateY(0);
  }
}

.animations .anim-ro {
  transform: translateX(50px);
  opacity: 0;

  &.showed {
    opacity: 1;
    transform: translateX(0) translateY(0);
  }

  @include media('<tablet') {
    transform: translateY(50px) translateY(0);
  }
}

.animations .anim-lo {
  transform: translateX(-50px);
  opacity: 0;

  @include media('<tablet') {
    transform: translateY(50px) translateX(0);
  }

  &.showed {
    opacity: 1;
    transform: translateX(0) translateY(0);
  }
}
