.disabled-scroll {
  overflow: hidden;

  #main, 
  #footer {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
}

.slick-slider {
  position: absolute;
  left: 0;
  right: 0;
  z-index: 1;
  height: calc(100vh - 70px);
  top: 70px;

  @include media('≥tablet') {
    height: calc(100vh - 89px);
    top: 89px;
  }

  &.touch-block {
    pointer-events: none;
  }

  .disabled-scroll & {
    position: fixed;
  } 

  & + #content {
    padding-top: calc(100vh - 70px);

    @include media('≥tablet') {
      padding-top: calc(100vh - 89px);
    }
  }

  .slide {
    overflow: hidden;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 0;

    .slide-img {
      @include animate(opacity, 1s);

      opacity: 0;
    }
    
    &.active {
      opacity: 1;

      .slide-img {
        opacity: 1;
      }
    }
  }

  .slick-dots {
    li {
      &:first-child {
        transform: rotate(180deg);
      }

      &.active {
        button {
          height: 9px;
        }
      }
    }

    button {
      transition: height 1s ease;
      padding: 0;
      height: 0;
    }
  }

  .title-change {
    opacity: 0;
    
    [data-active="0"] & {
      animation: titleSlide 2s;
      opacity: 1;
    }

    [data-active="1"] & {
      animation: titleSlideSec 2s;
      opacity: 1;
    }
  }

}

.decor-container {
  max-width: 1500px;
  margin: 0 auto;
  pointer-events: none;
  position: relative;
  overflow: hidden;
  height: 100%;
}

.slide-decor {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  transform: translate(0, 70%);
  height: calc(100vh - 70px);
  z-index: 1;
  opacity: 0.95;
  background-color: #515457; 
  // transition: transform 2s ease;

  @include media('≥tablet') {
    transform: translate(-50%, 0);
    height: calc(100vh - 89px);
  }

  [data-active="0"] & {
    animation: decor-reverse-mob 2s;
    transform: translate(0, 70%);
  
    @include media('≥tablet') {
      animation: decor-reverse 2s;
      transform: translate(-50%, 0);
    }
  }
  
  [data-active="1"] & {
    animation:  decor-mob 2s; 
    transform: translate(0, 70%);
  
    @include media('≥tablet') {
      animation:  decor 2s; 
      transform: translate(50%, 0);
    }
  }
}

.loader {
  width: 132px;
  height: 132px;
  position: relative;
  transform: scale(0.77);

  @include media('≥tablet') {
    transform: none;
  }

  svg {
    opacity: 0.73;
  }
}

.progressbar-text {
  font-size: 20px;

  @include media('≥tablet') {
    font-size: 16px;
  }
}


@keyframes decor-mob {
  0%  {
    opacity: 0.95; 
    transform: translateY(70%);
  }
  50% { 
    opacity: 1;
    transform: translateY(0);
  }
  80% { 
    opacity: 0.98;
    transform: translateY(-100%);
  }
  81% { 
    opacity: 1;
    transform: translateY(100%);
  }
  100% { 
    opacity: 0.95;
    transform: translateY(70%);
  }
}

@keyframes decor-reverse-mob {
  0%   {
    opacity: 0.95; 
    transform: translateY(70%);
  }
  50% { 
    opacity: 1;
    transform: translateY(0);
  }
  80% { 
    opacity: 0.98;
    transform: translateY(-100%);
  }
  81% { 
    opacity: 1;
    transform: translateY(100%);
  }
  100% { 
    opacity: 0.95;
    transform: translateY(70%);
  }
}

@keyframes decor {
  0%   {
    opacity: 0.95; 
    transform: translateX(-50%);
  }
  50% { 
    opacity: 1;
    transform: translateX(0);
  }
  100% { 
    opacity: 0.95;
    transform: translateX(50%);
  }
}

@keyframes decor-reverse {
  0%   {
    opacity: 0.95; 
    transform: translateX(50%);
  }
  50% { 
    opacity: 1;
    transform: translateX(0);
  }
  100% { 
    opacity: 0.95;
    transform: translateX(-50%);
  }
}

@keyframes titleSlide {
  0%   {
    opacity: 1; 
  }
  50% { 
    opacity: 0;
  }
  100% { 
    opacity: 1;
  }
}

@keyframes titleSlideSec {
  0%   {
    opacity: 1; 
  }
  50% { 
    opacity: 0;
  }
  100% { 
    opacity: 1;
  }
}