#wrapper {
  width: 100%;  background: $base-background-color;
  flex: 1 0 auto;
  position: relative;
  z-index: 1;
}

.container,
.content-container {
  @extend %clearfix;
  max-width: $max-width-center-container;
  padding: $padding-container;
  margin: 0 auto;

  @include media('≥desktop') {
    padding: 0 22px;
  }

  &.blog-container {
    padding: 0 15px;

    @include media('≥tablet') {
      padding: 0;
    }

    @include media('≥widescreen') {
      max-width: 1110px;
    }
  }
}

.content-container {
  max-width: 945px;

  &.quote-content  {
    max-width: 1000px;

    @include media('≥tablet') {
       max-width: 708px;
    }

    @include media('≥desktop') {
      max-width: 790px;
    }
    @include media('≥widescreen') {
      max-width: 945px;
    }
  }
}

.container-fluid {
  width: 100%;
}

.holder {
  @extend %clearfix;
  margin: 0 auto;
  position: relative;
  padding: $padding-container;

  @include media('≥tablet') {
    max-width: 600px;
  }

  @include media('≥desktop') {
    max-width: 760px;
  }

  &.section {
    padding-bottom: 0;
  }

  .iframe-section & {
    min-width: 320px;
  }
}

#main {
  position: relative;
  z-index: 1;
  padding: 70px 15px 0;
  overflow: hidden;

  @include media('≥tablet') {
    padding: 89px 15px 0;
  }

  @include media('≥desktop') {
    padding: 89px 22px 0;
  }
}

/*Loader page styles*/

#loader-page {
  transition: all 1s cubic-bezier(.165, .84, .44, 1);
  background-color: $light-beige;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  align-content: center;
  justify-content: center;
  overflow-x: hidden;
  overflow-y: auto;
  z-index: 10;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: 100vh;
  pointer-events: none;

  .container {
    margin: 0;
    min-width: 320px;
    
    @include media('≥tablet') {
      min-width: 450px;
    }

    @include media('≥widescreen') {
      min-width: 670px;
    }
  }

  .h1 {
    font-family: $base-font-serif;
    font-size: 24px;
    line-height: 1.2;
    margin: 0 0 16px;
    color: $gunmetal;

    @include media('≥tablet') {
      margin: 0 0 24px;
      font-size: 34px;
    }

    @include media('≥desktop') {
      font-size: 39px;
      margin: 0 0 28px;
    }

    @include media('≥widescreen') {
      font-size: 46px;
      margin: 0 0 32px;
    }

    span {
      margin-right: 4px;
    }
  }

  .logo {
    margin: 0 0 36px;
    width: 60px;

    @include media('≥tablet') {
      width: 88px;
      margin: 0 0 54px;
    }

    @include media('≥desktop') {
      width: 98px;
      margin: 0 0 60px;
    }

    @include media('≥widescreen') {
      width: 116px;
      margin: 0 0 70px;
    }

    img {
      width: 100%;
      display: block;
    }
  }
}

.animated-box,
.loader-box {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.details-box {
  justify-content: flex-end;
  margin: 0 0 71px;
  padding: 0 22px 0 0;

  @include media('≥tablet') {
    padding: 0 33px 0 0;
  }

  @include media('≥desktop') {
    padding: 0 6px 0 0;
  }

  @include media('≥widescreen') {
    margin: 0 0 62px;
    padding: 0 81px 0 0;
  }
}

.details {
  font-size: 9px;
  line-height: 1.5;
  text-transform: uppercase;
  font-weight: 400;
  color: $dusty-lavender;
  letter-spacing: 0.16em;

  @include media('≥tablet') {
    font-size: 14px;
    letter-spacing: 0.1em;
  }

  @include media('≥desktop') {
    font-size: 15px;
    letter-spacing: 0.15em;
  }

  @include media('≥widescreen') {
    font-size: 18px;
    letter-spacing: 0.13em;
    max-width: 50%;
  }
}

.loader {
  border-radius: 50%;
  width: 102px;
  height: 102px;
  border: 1px solid $light-gray;
  padding: 2px;
  position: relative;

  @include media('≥tablet') {
    width: 132px;
    height: 132px;
  }

  img {
    max-width: 100%;
  }
}

.percent {
  font-size: 10px;
  color: $light-gray;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);

  @include media('≥tablet') {
    font-size: 14px;
  }
}

/* Animation on a Loader page*/

.animated-box {
  -webkit-animation-name: fade;
  animation-name: fade;
  -webkit-animation-duration: 2s;
  animation-duration: 2s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

.animatedFirst {
  -webkit-animation-delay: 1s;
  animation-delay:1s;
}

.animatedSecond {
  -webkit-animation-delay: 1.5s;
  animation-delay:1.5s;
}

.animatedThird {
  -webkit-animation-delay: 2s;
  animation-delay: 2s;
}

.animated-loader {
  -webkit-animation-delay: 0;
  animation-delay: 0s;
}

@-webkit-keyframes fade {
  from {
    opacity: 0;
    visibility: hidden;
  }

  100% {
    opacity: 1;
    visibility: visible;
  }
}

@keyframes fade {
  from {
    opacity: 0;
    visibility: hidden;
  }

  100% {
    opacity: 1;
    visibility: visible;
  }
}

/*Page heading styles*/

.accordion-section {

  h3 {
    font-size: 22px;

    @include media('≥desktop') {
       font-size: 25px;
    }
  }

  .anchor-container {

    .anchor {

      &:after {

        @include media('≥desktop') {
          height: 120px;
        }
      }
    }
  }
}

.heading {
  background-color: $light-beige;
  z-index: 2;
  padding: 23px 0 76px;
  position: relative;
  color: $almost-black;
  margin-bottom: 40px;

  @include media('≥tablet') {
    padding: 30px 0 110px;
  }

  @include media('≥desktop') {
    padding: 30px 0 97px;
  }

  @include media('≥widescreen') {
    padding: 60px 0 104px;
  }

  .anchor {
    position: absolute;
    bottom: -45px;
    left: 50%;
    transform: translateX(-50%);
    width: 20px;
    height: 20px;
    color: $almost-black;
    font-size: 0;
    line-height: 0;
    z-index: 3;

    &:after {
      display: block;
      position: absolute;
      top: calc(100% + 13px);
      left: 50%;
      transform: translateX(-50%);
      width: 2px;
      height: 60px;
      content: '';
      background-color: $almost-black;
      margin: 0 0 0 1px;

      @include media('≥tablet') {
        height: 90px;
      }

      @include media('≥widescreen') {
        height: 120px;
      }
    }

    &:focus {
      outline: none;
    }
  }

  &:first-of-type {
    margin-bottom: 0;

    .anchor {
      &:before {
        @include animate(opacity);
        display: block;
        content: "\e900";
        font: 22px/1 'icomoon';
      }
    }
  }

  &.creme-section {
    background-color: $pinkish-grey;
    color: $white;

    h1 {
      color: $white;
    }

    .breadcrumbs {
      color: $white;

      a {
        color: $white;

        &:hover {
          opacity: 0.7;
          color: $white;
        }
      }
    }
  }

  &.details-heading {
    padding: 23px 0 76px;

    @include media('≥tablet') {
      padding: 30px 0 10px;
    }

    @include media('≥desktop') {
      padding: 30px 0 10px;
    }

    @include media('≥widescreen') {
      padding: 60px 0 10px;
    }

    .anchor {
      bottom: 33px;
    }
  }

  &.large-heading {
    padding: 0;
    
    .anchor {
       bottom: 45px;
    }

    .bg-stretch {
      background-position: 100% 100%;
      min-height: 400px;
      max-width: 4000px;
      margin: 0 auto;
      padding: 23px 0 76px;
      position: relative;
      @include media('≥tablet') {
        padding: 30px 0 110px;
        background-position: 80% 100%;
      }

      @include media('≥desktop') {
        padding: 30px 0 97px;
      }

      @include media('≥widescreen') {
        padding: 60px 0 104px;
        background-position: 100% 100%;
      }

      picture {
        position: absolute;
        bottom: 0;
        right: 0;

        img {
          max-width: 100%;
        }
      }

      .breadcrumbs {

        @include media('≥tablet') {
          padding: 0;
        }
      }
    }

    .breadcrumbs {
      margin-bottom: 336px;

      @include media('≥tablet') {
        margin-bottom: 25px;
        justify-content: flex-start;
      }

      @include media('≥widescreen') {
        margin-bottom: 32px;
      }
    }

    h1 {
      font-size: 35px;

      @include media('≥tablet') {
        font-size: 60px;
      }
    }

    .holder {

      @include media('≥desktop') {
        padding-left: 15px;
      }
    }
  }

  h1 {
    color: $almost-black;
  }

  .holder {

    @include media('≥desktop') {
      padding-left: 105px;
    }
    
    @include media('≥widescreen') {
      padding-left: 15px;
    }
  }

  .breadcrumbs {
    padding: 0 0 0 48px;
    margin-bottom: 127px;
    display: block;

    @include media('≥tablet') {
      margin-bottom: 140px;
      display: flex;
      padding: 0 0 0 110px;
    }

    @include media('≥desktop') {
      margin-bottom: 70px;
      padding: 0;
    }

    @include media('≥widescreen') {
      margin-bottom: 44px;
    }

    li {
      display: inline;

      @include media('≥tablet') {
        display: block;
      }
    }
  }

  .lead-text {
    color: $white;
    font-weight: 600;
    font-size: 18px;
    line-height: 25px;
    padding-top: 10px;

    @include media('≥tablet') {
      font-size: 25px;
      line-height: 34px;
      padding-top: 98px;
    }

    @include media('≥desktop') {
      padding-top: 53px;
    }

    @include media('≥widescreen') {
      padding-top: 122px;
    }

    p {
      &:last-of-type {
        margin-bottom: 0;
      }
    }
  }

  & + .section {
    padding-top: 60px ;

    @include media('≥tablet') {
      padding-top:92px ;
    }

    @include media('≥desktop') {
      padding-top: 87px ;
    }

    @include media('≥widescreen') {
      padding-top: 116px ;
    }
  }

  &.overview-section {
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    margin: 0 -15px;

    @include media('≥tablet') {
      margin: 0;
    }

    .bg-stretch {
      min-height: 237px;
      width: 100%;
      background-size: cover;

      @include media('≥tablet') {
        min-height: 415px;
      }

      @include media('≥desktop') {
        min-height: 460px;
        width: 50%;
      }

      @include media('≥widescreen') {
        min-height: 650px;
        width: 55%;
      }
    }

    .holder {
      @include animate(background-color);
      max-width: 100%;
      background-color: $gunmetal;
      color: $white;
      display: block;
      display: flex;
      flex-wrap: wrap;
      align-items: flex-end;
      padding-left: 15px;
      font-size: 16px;
      font-weight: 400;
      line-height: 1.44;

      @include media('≥tablet') {
        font-size: 18px;
        font-weight: 600;
        line-height: 1.67;
      }

      @include media('≥desktop') {
        width: 50%;
      }

      @include media('≥widescreen') {
        width: 45%;
      }

      &:hover {
        background-color: $pinkish-grey;
      }

      &:focus {
        outline: none;
      }

      h1 {
        color: $white;
        letter-spacing: 0;
        font-size: 25px;

        @include media('≥tablet') {
          margin-bottom: 15px;
          font-size: 30px;
          line-height: 37px;
        }

        @include media('≥widescreen') {
          margin-bottom: 30px;
          font-size: 45px;
          line-height: 52px;
        }
      }
    }

    .lead-text {
      padding-top: 0;
      width: 100%;
    }

    .text-holder {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      padding: 30px 15px 56px;

      @include media('≥tablet') {
        padding: 60px 70px 80px;
      }

      @include media('≥desktop') {
        padding: 0 30px 80px;
      }

      @include media('≥widescreen') {
        max-width: 70%;
        padding: 0 45px 90px;
      }
    }

    .anchor {
      bottom: 45px;
    }

    & + .section {
      &:last-of-type {
        padding-bottom: 0;
      }
    }
  }
}

.light-background {
  background-color: $light-beige;

  h1 {
    color: $almost-black;
  }

  .breadcrumbs {
    color: $dusty-lavender;

    a {
      color: $dusty-lavender;

      &:hover {
        opacity: 0.8;
      }
    }
  }
}

.breadcrumbs {
  @extend %listreset;
  display: flex;
  flex-wrap: wrap;
  text-transform: uppercase;
  color: $dusty-lavender;
  font-size: 14px;
  line-height: 1.2;
  justify-content: flex-start;
  padding: 0 0 0 48px;

  @include media('≥tablet') {
    padding: 0;
  }

  li {
    padding: 0 0 5px;

    +li {
      &:before {
        content: '/';
        margin: 0 4px;
      }
    }
  }

  a {
    @include animate(opacity color);
    color: $dusty-lavender;

    &:hover {
      color: $almost-black;
    }
  }
}

/*Google map styles*/

#google-map {
  width: 100%;
  position: relative;
  height: 290px;
  z-index: 1;

  @include media('≥tablet') {
   height: 358px;
  }

  @include media('≥desktop') {
    height: 386px;
  }

  iframe {
    width: 100%;
    height: 100%;
    margin: 0;
    vertical-align: top;
    border: none;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
  }
}

/* Anchor styles */

.anchor-container {
  text-align: center;

  .anchor-section & {
    padding-bottom:36px;
  }

  .light-background & {
    padding: 0;
  }

  .image-section & {
    padding-top: 0;
  }
}

.anchor {
  display:inline-block;
  vertical-align: top;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 400;
  color: $almost-black;
  position: relative;

  @include media('≥tablet') {
    font-size: 14px;
  }

  &:after {
    display: block;
    position: absolute;
    top: calc(100% + 13px);
    left: 50%;
    transform: translateX(-50%);
    width: 2px;
    height: 60px;
    content: '';
    background-color: $almost-black;
    z-index: 2;

    @include media('≥tablet') {
      height: 90px;
    }

    @include media('≥widescreen') {
      height: 120px;
    }
  }

  &:hover {
    color: $pinkish-grey;
  }

  &:focus {
    outline: none;
  }

  &.infinitely{

    &:before {
      animation: top-down 6s infinite ;
      color: $almost-black;
    }
  }

  .single-content & {

    &:after {

      @include media('≥tablet') {
        height: 60px;
      }
    }
  }

  &.slick-scroll {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    width: 20px;
    height: 20px;
    color: $almost-black;
    font-size: 0;
    line-height: 0;
    z-index: 3;
    bottom: auto;
    top: calc(100vh - 63px);

    @include media('≥desktop') {
      top: calc(100vh - 65px);
    }

    &:hover {
       
      &:before {
        opacity: 0.7;
      }
    }

    &:focus {
      outline: none;
    }

    &:before {
      @include animate(opacity);
      display: block;
      content: "\e900";
      font: 22px/1 'icomoon';
    }
  }
}

@-webkit-keyframes top-down {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-10px);
  }
  60% {
    transform: translateY(-10px);
  }
}

@keyframes top-down {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-10px);
  }
  60% {
    transform: translateY(-10px);
  }
}

/*Section styles*/

.section {
  @extend %clearfix;
  padding-top: 40px;
  padding-bottom: 40px;
  position: relative;
  z-index: 1;

  @include media('≥tablet') {
    padding-top: 65px ;
    padding-bottom: 35px;
  }

  &.top-anchor {
    z-index: 0;
    padding-top: 60px ;
    padding-bottom: 32px;
    margin-top: 65px;

    @include media('≥tablet') {
      padding-top:92px ;
      padding-bottom: 60px;
    }

    @include media('≥desktop') {
      padding-top: 87px ;
    }

    @include media('≥widescreen') {
      padding-top: 116px;
      margin-top: 88px;
    }

    &.accordion-section {
      padding-bottom: 8px;

      @include media('≥tablet') {
        padding-bottom: 20px;
      }

      @include media('≥desktop') {
        padding-bottom: 60px;
      }

      & + .accordion-section {
        margin-top: 114px;

        @include media('≥widescreen') {
          margin-top: 124px;
        }
      }
    }

    .anchor-container {
      position: absolute;
      top: -125px;
      left: 50%;
      transform: translateX(-50%);
      width: 100%;

      @include media('≥tablet') {
        top: -153px;
      }

      @include media('≥widescreen') {
        top: -180px;
      }
    }
  }

  &.treatments-section {

   .treatments-columns {

     h3 {
      margin: 0 0 15px;
      max-width: 80%;
      font-size: 25px;

      @include media('≥tablet') {
        margin: 0 0 30px;
        font-size: 30px;
      }

      @include media('≥desktop') {
        max-width: 100%;
      }
     }

     .column {

      & + .column {
        h3 {

          @include media('<tablet') {
            margin: 10px 0px 30px;
          }
        }
      }
     }
    }
  }
}

