    line-height: 30px;
  }

  @include media('≥desktop') {
    font-size: 25px;
    line-height: 34px;
  }

  .blog-details & {
    @include media('≥desktop') {
      font-size: 20px;
      line-height: 30px;
    }
  }
}

.creme-section,
.pinkish-section {
  color: $white;

  .page-lead {
    color: $white;

    p {
