  }

  a {
    color: $white;

    &:hover {
      color: $gunmetal;
    }
  }
}

.creme-section {
  background: $creme;
}

.pinkish-section {
 background: $pinkish-grey;
}

/*treatments section styles */

.treatments-columns {
 display: flex;
 flex-wrap: wrap;
 width: auto;
 
 @include media('≥tablet') {
  margin: 0 8px;
 }

 @include media('≥desktop') {
   margin: 0 -8px;
 }

 .column {
  width: 100%;

  @include media('≥tablet') {
    width: 50%;
    padding: 0 8px;
  }

  &:last-of-type {
    .treatments-list {

      li {
        &:last-of-type {
          @include media('<tablet') {
            padding: 0;
          }
        }
      }
    }
  }
 }
}

.treatment-box {
  position: relative;
  width: 100%;
  display: block;
  transition: all .8s cubic-bezier(.165, .84, .44, 1);
  overflow: hidden;
  font-weight: 400;

  &:hover {
    transform: scale(1.05);
    z-index: 2;

    .kumya {
      color: $white;

      &:after {
        -webkit-transform: translate3d(0,0,0);
         transform: translate3d(0,0,0);
      }

      .data {
        display: inline-block;
        position: relative;
        color: $white;

        &:before {
          width: 0;
        }
      }
    }
  }

  .bg-stretch {
    min-height: 344px;
    background-color: $light-beige;
    overflow: hidden;
  }

  .item {
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: 2;
    width: 100%;
    transform: none;
    text-align: center;
    font-size: 18px;
    line-height: 1.2;
    transition: all .8s cubic-bezier(.165, .84, .44, 1);
  }

  .kumya {
    width: 100%;
    padding: 14px 5px 18px;
    color: #393836;
    background-color: $light-beige;
    border-top: 2px solid rgba(255,255,255,1);

    &:hover {
       .data {
        color: $white;

        &:before {
          width: 0;
        }
      }
    }
  }
}

.bg-stretch {
  background-position: 50% 50%;
  background-repeat: no-repeat;
  position: relative;

  @include media('<desktop') {
    background-size: cover;
  }
}

.treatments-list {
  @extend %listreset;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: auto;

  @include media('≥tablet') {
    margin: 0 0 -30px;
  }

  @include media('≥desktop') {
    margin: 0 0 -64px;
  }

  &.home-treatments {

    @include media('≥tablet') {
       margin: 0 -8px -30px;
    }

    @include media('≥desktop') {
       margin: 0 -8px -64px;
    }

    li {

      @include media('≥tablet') {
        width: 50%;
        padding: 0 8px 30px;
      }

      @include media('≥desktop') {
        padding: 0 8px 64px;
      }
    }
  }

  li {
    position: relative;
    width: 100%;
    padding: 0 0 20px;
    z-index: 1;

    @include media('≥tablet') {
      width: 20%;
      padding: 0 1px 30px;
    }

    @include media('≥desktop') {
      padding: 0 1px 64px;
    }

    &.active {
      z-index: 2;

      .treatment-box {

        .kumya {

          &:after {
            -webkit-transform: translate3d(0,0,0);
             transform: translate3d(0,0,0);
          }

          .data {
            display: inline-block;
            position: relative;
            color: $white;

            &:before {
              width: 0;
            }
          }
        }
      }
    }

    &:hover {
      z-index: 2;
    }
  }
}

.treatment-title {

  h2 {
    margin-bottom: 30px;
    font-size: 25px;

    @include media('≥tablet') {
      margin-bottom: 60px;
      font-size: 30px;
    }

    @include media('≥desktop') {
      font-size: 35px;
    }

    @include media('≥widescreen') {
      margin-bottom: 70px;
    }
  }
}

/*Teaser section styles*/

.teaser-section {
    
  .content-container {
    display: flex;
    flex-wrap: wrap;
    position: relative;
    justify-content: center;

    @include media('≥tablet') {
      flex-wrap: nowrap;
      max-width: 700px;
    }

    @include media('≥desktop') {
      max-width: 945px;
    }
  }

  .image-container {
    position: relative;
    z-index: 0;
    margin: 0 0 7px;
  }

  .text-container {
    position: relative;
    z-index: 1;
    width: 100%;
  }

  &.left-picture {
    .content-container {

      .image-container {
        
        @include media('≥tablet') {
          margin: 0;
          order: 1;
        }

        @include media('≥widescreen') {
          margin: 0 0 0 -96px;
        }
      }

      .text-container {
        

        @include media('≥tablet') {
          margin: 0 0 0 -102px;
          padding: 20px 0 0;
          order: 2;
        }

        @include media('≥desktop') {
          padding-top: 74px;
          margin: 0 0 0 -96px;
        }

        @include media('≥widescreen') {
          margin: 0 0 0 -80px;
        }
      }
    }

    img {
      max-width: 340px;

      @include media('≥tablet') {
        max-width: 366px;
      }

      @include media('≥desktop') {
        max-width: 560px;
      }
    }
  }

  &.right-picture {
    .content-container{

      .image-container {
        
        @include media('≥tablet') {
          order: 2;
          margin: 0;
        }

        @include media('≥desktop') {
          margin: 0 0 0 -80px;
        }

        @include media('≥widescreen') {
          margin: 0 -100px 0 -80px;
        }
      }

      .text-container {
        
        @include media('≥tablet') {
          padding: 45px 0 0;
          margin: 0 -120px 0 0;
          order: 1;
        }

        @include media('≥desktop') {
          padding-top: 40px;
          margin: 0;
        }

        @include media('≥widescreen') {
          padding-top: 67px;
        }
      }
    }

    img {
      max-width: 320px;

      @include media('≥tablet') {
       max-width: 350px;
      }

      @include media('≥desktop') {
        max-width: 450px;
      }

      @include media('≥widescreen') {
        max-width: 540px;
      }
    }
  }

  &.center-picture {

    .image-container {
      margin: 10px 0 -126px -72px;

      @include media('≥tablet') {
         margin: 12px -90px 0 0;
      }

      @include media('≥desktop') {
         margin: 23px -100px 0 0;
      }
      @include media('≥widescreen') {
         margin: 0 -100px 0 85px;
      }
    }

    .text-container {

      @include media('≥tablet') {
        width: 71%;
        padding: 64px 80px 0 0;
      }

      @include media('≥widescreen') {
         width: 60%;
         padding: 48px 0 0 0;
       }
    }

    .content-container {
     @include media('<tablet') {
        justify-content: flex-start;
      }
    }

    img {
      max-width: 175px;

      @include media('≥desktop') {
        max-width: 216px;
      }
    }

    h2 {
      
      @include media('<tablet') {
        margin-bottom: 28px;
      }
    }
  }

  h2 {
    color: $light-burgundy;
    margin-bottom: 10px;

    @include media('≥tablet') {
      margin-bottom: 20px;
    }
    
    @include media('≥desktop') {
      margin-bottom: 42px;
    }
  }
}

.accordion-section {
