  width: 100%;
  padding: 60px 0;
  position: relative;
  font-size: 16px;
  line-height: 1.2;
  justify-content: center;
  border-bottom: 1px solid $white;
  font-weight: 400;

  @include media('≥tablet') {
     width: 50%;
     border-bottom: 0;
  }

  @include media('≥desktop') {
     font-size: 18px;
  }

  &:hover {
    color: $white;
    background-color: $pinkish-grey;
  }

  .holder {
    max-width: 90%;
    text-align: center;
    margin: 0;
    position: static;

