.link-content {
  width: 100%;
  margin: 0 auto;
  max-width: 400px;
}

.link-text {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  width: 100%;
  position: absolute;
  bottom: 33px;
  right: 30px;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 1.2;

  @include media('≥tablet') {
    bottom: 30px;
   }

  @include media('≥desktop') {
     bottom: 27px;
     right: 40px;
   }

  &:after {
    content: "\e905";
    display: inline-block;
    vertical-align: middle;
    font: 16px/1 'icomoon';
    margin: 0 0 0 20px;
  }
}

/*accordion styles*/

.accordion {
  @extend %listreset;
  counter-reset: accordionCount;
  max-width: 680px;
  margin: 0 auto;

  @include media('≥desktop') {
    max-width: 100%;
  }

  .item {
    display: block;
    background-color: $white;
    position: relative;
    font-size: 18px;
    line-height: 1.2;
    font-family: $base-font-serif;
    font-weight: 400;

    @include media('≥tablet') {
      font-size: 20px;
    }

    @include media('≥desktop') {
      font-size: 25px;
    }
  }

  .kumya {
    display: block;
    padding: 20px;
    position: relative;
    padding: 17px 50px 17px 16px;

    @include media('≥tablet') {
     padding: 30px 70px 30px 70px;
    }

    @include media('≥desktop') {
     padding: 30px 80px 30px 94px;
    }

    &:before {
      display: none;

      @include media('≥tablet') {
        top: 51%;
        left: 41px;
        transition-timing-function: cubic-bezier(0.7,0,0.3,1);
        transition: color 0.5s;
        counter-increment: accordionCount;
        content:''counter(accordionCount, decimal-leading-zero)'. ';
        display: inline-block;
        vertical-align: top;
        position: absolute;
        transform: translateY(-50%);
        z-index: 2;
        font-size: 12px;
        line-height: 1.2;
        font-family: $base-font-sans-serif;
      }

      @include media('≥desktop') {
        left: 65px;
        top: 52%;
      }
    }

    &:hover {

      &:before {
        color: $white;
      }
    }
  }

  > li {
    position: relative;
    margin-bottom: 10px;

    &.empty-slide {
       & a {
        &:after {
          content: "\e90a";
        }
      }
    }

    &:after {
      display: none;
    }

    &.active > a,
    & > a:hover {

      &:before {
        border-color: $white;
      }

      &:after {
       color: $white;
       transform: scale(1,-1) translateY(50%);
      }

      &.empty-slide {

       &:after {
          transform:translateY(-50%);
        }
      }

      .kumya {

        &:after {
          -webkit-transform: translate3d(0,0,0);
           transform: translate3d(0,0,0);
        }

        &:before {
          color: $white;
        }

        .data {
          color: $white;

          &:before {
            width: 0;
          }
        }
      }
    }

    &.active {
      
     > a {
       box-shadow: 0 1px 10px 0 rgba(0, 0, 0, 0.3);
     }
    }

    > a {
      position: relative;

      &:before {
        position: absolute;
        width: 35px;
        height: 35px;
        border-radius: 50%;
        border: 1px solid $dusty-lavender;
        top: 50%;
        right: 15px;
        content: "";
        transition-timing-function: cubic-bezier(0.7,0,0.3,1);
        transition: border-color 0.5s;
        z-index: 2;
        pointer-events: none;
        transform: translateY(-50%);

        @include media('≥tablet') {
          width: 50px;
          height: 50px;
        }

        @include media('≥desktop') {
          width: 60px;
          height: 60px;
        }
      }

      &:after {
        font: 14px/33px 'icomoon';
        transition-timing-function: cubic-bezier(0.7,0,0.3,1);
        transition: color 0.5s, scale 0.5s, translate 0.5s;
        content: "\e909";
        position: absolute;
        right: 26px;
        top: 50%;
        pointer-events: none;
        color: $almost-black;
        transform: translateY(-50%);

        @include media('≥tablet') {
          right: 32px;
          font: 16px/48px 'icomoon';
          top: 51%;
        }

        @include media('≥desktop') {
          font: 20px/58px 'icomoon';
          right: 35px;
        }
      }
    }
  }

  .slide {
    background-color: $white;
    padding: 20px 0 10px;

    @include media('≥tablet') {
      padding: 30px 0 15px;
    }

    p {
      @include media('<desktop') {
        margin-bottom: 22px;
      }
    }
  }

  .holder {
    @include media('≥tablet') {
      max-width: 575px;
    }

    @include media('≥desktop') {
      max-width: 760px;
    }
  }
}

