#footer {
  flex-shrink: 0;
  padding: 0 15px 15px;
  font-size: 16px;
  line-height: 1.2;

  @include media('≥tablet') {
    line-height: 1.5;
  }

  @include media('≥desktop') {
    font-size: 18px;
    line-height: 1.2;
    padding: 0 22px 22px;
  }

  .timetable {
    width: 100%;
    margin: 0 0 0 48px;
    max-width: 190px;

    @include media('≥tablet') {
      margin: 0;
      max-width: 100%;
    }

    dt {
      padding-right: 30px;
    }
  }

  .social-links {
    width: 100%;
    justify-content: center;

    li {
      padding: 0 19px 5px 0;
    } 

    a {
      color: $gunmetal;

      &:hover {
        color: $pinkish-grey;
      }
    }
  }
}

.footer-container {
  padding: 75px 0 50px;
  background: $light-beige;

  @include media('≥tablet') {
    padding: 80px 0 75px;
  }

  @include media('≥desktop') {
    padding: 90px 0 60px;
  }
}

.footer-menu {
  @extend %listreset;
  width: 100%;
  margin-bottom: 16px;
  text-align: center;

  @include media('≥tablet') {
    text-align: left;
  }

  li {
    margin-bottom: 5px;
    display: inline-block;
    vertical-align: top;
    width: 100%;

    @include media('≥desktop') {
     margin-bottom: 7px;
    }
  }

  a {
    color: $gunmetal;
    font-weight: 400;

    &:hover {
       color: $pinkish-grey;
    }
  }
}

.footer-columns {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 auto;
  max-width: 640px;

   @include media('≥desktop') {
     max-width: 740px;
  }

  @include media('≥widescreen') {
     max-width: 780px;
  }

  .column {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    align-content: flex-start;
    justify-content: center;

    @include media('≥tablet') {
      width: 50%;
    }

    &:first-of-type {
      padding: 0 0 35px;
     
     @include media('≥tablet') {
      padding: 16px 0 0;
    }

     @include media('≥desktop') {
        padding: 8px 0 0;
      }
    }

    &:last-of-type {

     @include media('≥tablet') {
       justify-content: flex-end;
       padding-left: 45px;
     }

       @include media('≥desktop') {
        padding-left: 110px;
      }

      @include media('≥widescreen') {
        padding-left: 96px;
      }
    }
  }
}

.address {
  font-style: normal;
  width: 100%;
  margin: 0 0 37px;
  text-align: center;

  @include media('≥tablet') {
    text-align: left;
  }

  @include media('≥desktop') {
    margin: 0 0 50px;
  }
}

.logo-title  {
  display: inline-block;
  vertical-align: top;
  width: 100%;
  font-family: $base-font-serif;
  font-size: 26px;
  line-height: 1.2;
  text-align: center;
}

.footer-logo {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 48px;

  .logo {
    margin-bottom:38px;
    display: block;
    width: 64px;

    img {
      display: block;
      width: 100%;
    }
  }
}
