ul,
ol,
dl,
p,
h1,
h2,
h3,
h4,
h5,
h6,
address,
form,
table,
blockquote,
applet,
embed,
object,
iframe,
frameset {
  margin: 0 0 $vertical-rhythm;
}

blockquote {
  border-left: none;
  padding-left: 0;
  margin-left: 0;
  background-color: $dusty-lavender;
  color: $white;
  padding: 30px;
  font-size: 25px;
  line-height: 32px;
  font-family: $base-font-serif;

  @include media('≥tablet') {
    font-size: 30px;
    line-height: 38px;
    padding: 30px 70px;
  }

  @include media('≥desktop') {
    padding: 40px 94px 50px;
    font-size: 36px;
    line-height: 48px;
  }

  @include media('≥widescreen') {
    padding: 50px 100px 56px 100px;
  }

  a {
    color: $white;

    &:hover {
      color: $pinkish-grey;
    }
  }

  &.blockquote {
    margin-bottom: 0;
  }
}

table {
  border-collapse: collapse;
}

th,
td {
  border: 1px solid $gray;
  padding: 3px;
  text-align: left;
  vertical-align: middle;
}

th {
  text-align: center;
  vertical-align: middle;
}

ul,
ol {
  padding-left: 20px;
}

ul ul,
ul ol,
ol ul,
ol ol {
  padding-left: 20px;
  margin: 0;
}

ul {
  list-style-type: disc;
}

dl {
  @extend %clearfix;

  dt {
    float: left;
    clear: left;
    padding-right: 0.3em;
    font-weight: bold;
  }

  dd {
    overflow: hidden;
  }

  &.termins {
    font-size: 20px;
    margin-bottom: 30px;

   dt {
      font-weight: 400;

      @include media('<tablet') {
        width: 100%;
        padding-right: 0;
      }
    }

    dd {
      font-weight: 700;
      overflow: visible;
      margin: 0;
      
      @include media('<tablet') {
        width: 100%;
        margin: 0 0 31px;
        padding: 0;

        &:last-of-type {
          margin-bottom: 0;
        }
      }
    }

    a {
      color: $white;

      &:hover {
        color: $light-burgundy;
      }
    }
  }
}

pre {
  max-width: 100%;
  overflow: auto;
}
