  @media (max-width: 768px){
    .heading.large-heading .bg-stretch.center-hand picture {
      right: auto;
      left: 50%;
      -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
      -o-transform: translateX(-50%);
      transform: translateX(-50%);
    }
  	.heading.large-heading {
  		overflow: hidden;
  	}
    .heading.large-heading + section:before {
	    content: '';
    	display: block;
    	position: absolute;
	    top: -13px;
	    left: 50%;
	    transform: translateX(-50%);
	    width: 2px;
	    height: 60px;
	    background-color: #0a0f0d;
	    margin: 0 0 0 1px;
    }
    .heading.large-heading .bg-stretch {
      display: -webkit-flex;
      display: -moz-flex;
      display: -ms-flex;
      display: -o-flex;
      display: flex;
    }
    .heading.large-heading .holder  {
      display: -webkit-flex;
      display: -moz-flex;
      display: -ms-flex;
      display: -o-flex;
      display: flex;
    }
    .heading.large-heading .text-holder {
      display: -webkit-flex;
      display: -moz-flex;
      display: -ms-flex;
      display: -o-flex;
      display: flex;
      -webkit-flex-direction: column;
      -moz-flex-direction: column;
      -ms-flex-direction: column;
      -o-flex-direction: column;
      flex-direction: column;
      justify-content: space-between;
    }
    .heading.large-heading .breadcrumbs {
      flex-grow: 1;
    }
  }

  .treatment-box .bg-stretch {
    -webkit-background-size: cover;
    background-size: cover;
  }

  #content {
    overflow: hidden;
  }