// Colors
// Color calculator - http://razorjam.github.io/sasscolourfunctioncalculator/
// Color names - http://name-of-color.com/

$gray:               #979797;
$light-gray:         #8d8f90;
$gunmetal:           #515457;
$almost-black:       #0a0f0d;
$black:              $almost-black;
$white:              #fff;
$blue:               #5b759e;
$pinkish-grey:       #d1bfbe;
$light-beige:        #f6f6f4;
$dusty-lavender:     #a7a2a3;
$pinkish-grey:       #d1bfbe;
$light-burgundy:     #92374d;
$orangey-red:        #ff3737;
$green:              #009982;
$creme:              #dfc0be;
$silver:             #d7dedc;

$base-font-sans-serif:        'ProximaNova', 'Arial', 'Helvetica Neue', 'Helvetica', sans-serif;
$base-font-serif:             'NewParis', 'Times New Roman', 'Times', 'Baskerville', 'Georgia', serif;
$add-font-serif:              'Lora', 'NewParis', 'Times New Roman', 'Times', 'Baskerville', 'Georgia', serif;
$base-font:                   $base-font-sans-serif;

// Body
$base-text-color:             $gunmetal;
$base-background-color:       $white !default;
$base-background-body:        $white !default;
$base-background-footer:      $base-background-body !default;
$font-size-base:              16px;
$line-height-base:            1.45;
$font-size-base-lagre:        20px;
$line-height-base-lagre:      1.5;
$base-font-family:            $base-font !default;
$base-min-width:              320px;

$max-width-center-container:  1410px;
$padding-container:           0 15px;

// font weight
$thin:                        100;
$light:                       300;
$normal:                      400;
$medium:                      500;
$semibold:                    600;
$bold:                        700;
$black-font:                  800;

// default margins
$vertical-rhythm:             $font-size-base + 4;
$widget-margin:               20px;

// Liks
$base-link-color:             $gunmetal;
$base-link-hover-color:       $light-burgundy;
$text-decoration:             none;
$text-decoration-hover:       none;

// Headers size
$title-palette: (
  mobile: (
    'h1': 30,
    'h2': 25,
    'h3': 22,
    'h4': 14,
    'h5': 14,
    'h6': 14
  ),
  tablet: (
    'h1': 38,
    'h2': 30,
    'h3': 30,
    'h4': 20,
    'h5': 18,
    'h6': 16
  ),
  desktop: (
    'h1': 48,
    'h2': 35,
    'h3': 25,
    'h4': 20,
    'h5': 18,
    'h6': 16
  ),
  widescreen: (
    'h1': 48,
    'h2': 35,
    'h3': 25,
    'h4': 20,
    'h5': 18,
    'h6': 16
  )
);
$headings-font-family:              $base-font-serif;
$headings-color:                    $almost-black;

// Buttons
$btn-color:                         $gunmetal;
$btn-background:                    $white;
$btn-border:                        1px solid $black;
$btn-padding:                       12px 20px;
$btn-font-size:                     18px;
$btn-line-height:                   1.44;
// hover
$btn-hover-background:              $pinkish-grey;
$btn-hover-color:                   $white;

// Forms
$input-font-size:                   16px; // font size for forms elements
$input-line-height:                 $btn-line-height; // line height for forms elements
$input-padding:                     $btn-padding !default; // padding for forms elements
$input-border:                      1px solid $pinkish-grey; // border for forms elements
$input-border-radius:               0 !default; // border radius for forms elements
$input-height:                      50px; // height for forms elements
$input-font-family:                 $base-font;
//submit
$submit-background:                 $btn-background;
$submit-color:                      $btn-color;
$submit-height:                     $input-height;
$submit-border:                     $btn-border;
$submit-padding:                    $input-padding;
$submit-font-size:                  $input-font-size;
$submit-line-height:                $input-line-height;
// submit hover
$submit-hover-background:           $btn-hover-background; // submit background hover
$submit-hover-color:                $btn-hover-color; // submit color hover
//focus
$input-focus-border-color:          lighten( $pinkish-grey, 10% );
//placeholder
$placeholder-color:                 $dusty-lavender;
// select
$select-height:                     $input-height;
$select-border:                     $input-border;
$select-border-radius:              $input-border-radius;
$select-padding:                    $input-padding;
$select-font-size:                  $input-font-size;
$select-line-height:                $input-line-height;


// nav
$nav-color:                         $black;
$mobile-nav-drop-bg:                $black;
$mobile-nav-drop-color:             $white;

$animation-speed:                   0.3s;
