.slider-container {
  position: relative;
  z-index: 2;

  .anchor {
    bottom: 44px;
  }
}

.slick-slider {
  position: relative;
  z-index: 1;
  border-color: $white;
  border-width: 0 15px;
  border-style: solid;

  @include media('≥desktop') {
    border-width: 0 22px;
  }

  .slide {
    height: calc(100vh - 70px);
    display: flex;
    flex-wrap: wrap;
    align-items: flex-end;
    padding: 0 0 18vh;

     @include media('≥tablet') {
      height: calc(100vh - 89px);
      padding: 0 0 110px;
    }

    @include media('≥desktop') {
      padding: 0 0 92px;
    }

    @include media('≥widescreen') {
      padding: 0 0 115px;
    }
  }

  .slide-img {
    background-size: cover;
    background-position: 50% 50%;
    background-repeat: no-repeat;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;

    &.mobile-view {
      display: block;
      @include media('≥tablet') {
        display: none !important;
      }
    }

    &.desktop-view {
      
      @include media('<tablet') {
        display: none !important;
      }
    }
  }

  .slick-dots {
    @extend %listreset;
    position: absolute;
    bottom: 26px;
    right: 30px;
    z-index: 1;
    
    button {
      border: none;
      text-indent: -9999px;
      overflow: hidden;
      display: block;
      background-color: transparent;
      padding: 4px;
      height: 9px;
      width: 9px;
      background-color: $white;

      &:hover {
        background-color: transparent;
      }
    }

    li {
      @include animate(background-color);
      margin: 10px 0 0;
      border: 1px solid $white;
      height: 10px;
      width: 10px;

      &.slick-active {
        background-color: $white;
      }

      &:hover {
        background-color: $white;
      }
    }
  }

  h1 {
    font-size: 40px;
    font-weight: bold;
    line-height: 1.08;
    color: $white;
    text-shadow: 0 1px 4px rgba(0, 0, 0, 0.5);
    font-family: $base-font-serif;

    @include media('≥tablet') {
      font-size: 70px;
      line-height: 1.2;
    }

    @include media('≥desktop') {
      font-size: 85px;
    }
    @include media('≥widescreen') {
      font-size: 98px;
      line-height: 1.1;
    }
  }

  .container {
    max-width: 570px;
    position: relative;
    z-index: 2;

    @include media('<tablet') {
      padding: 0 30px;
    }

    @include media('≥desktop') {
      max-width: 950px;
    }
  }

  & + #content {

    & > .space {

      &:first-of-type {
        padding-top: 60px ;
        padding-bottom: 32px;

        @include media('≥tablet') {
          padding-top:92px ;
          padding-bottom: 60px;
        }

        @include media('≥desktop') {
          padding-top: 87px ;
        }

        @include media('≥widescreen') {
          padding-top: 116px ;
        }
      }
    }
  }
}

.slide-title {
  font-size: 16px;
  line-height: 1.44;
  letter-spacing: 0.8px;
  color: $white;
  position: relative;
  display: inline-block;
  vertical-align: top;
  max-width: 68%;
  margin-bottom: 18px;
  padding-bottom: 15px;

  @include media('≥tablet') {
    font-size: 20px;
    line-height: 1.5;
    letter-spacing: 1px;
    margin-bottom: 23px;
    padding-bottom: 18px;
    max-width: 100%;
  }

  @include media('≥desktop') {
    padding-bottom: 18px;
    margin-bottom: 12px;
  }

  @include media('≥widescreen') {
    font-size: 22px;
    line-height: 1.36;
    margin-bottom: 26px;
    padding-bottom: 30px;
  }
  
  &:before {
    position: absolute;
    bottom: 0;
    left: 0;
    content: '';
    background-color: $white;
    width: 60px;
    height: 2px;
    display: block;

    @include media('≥widescreen') {
      height: 3px;
    }
  }
}

.slide-decor {
  position: absolute;
  top: 0;
  left: 50%;
  right: 0;
  bottom: 0;
  z-index: 1;
  opacity: 0.95;
  background-color: $gunmetal;
}
